const aboutMenu = document.querySelector('.about-menu__list');
if(aboutMenu) {
    const aboutMenuActiveItem = aboutMenu.querySelector('.about-menu__list .about-menu__link--active');
    const aboutMenuX = aboutMenuActiveItem.offsetWidth + 'px';
    const aboutMenuY = aboutMenuActiveItem.offsetLeft + 'px';
    aboutMenu.style.setProperty('--underline-width', aboutMenuX);
    aboutMenu.style.setProperty('--underline-offset-x', aboutMenuY);

    aboutMenu.addEventListener('mouseover', (event) => {
        if (event.target.classList.contains('about-menu__link')) {
        aboutMenu.style.setProperty(
            '--underline-width',
            `${event.target.offsetWidth}px`
        );
        aboutMenu.style.setProperty(
            '--underline-offset-x',
            `${event.target.offsetLeft}px`
        );
        }
    });

    aboutMenu.addEventListener('mouseleave', () => {
        aboutMenu.style.setProperty('--underline-width', aboutMenuX);
        aboutMenu.style.setProperty('--underline-offset-x' , aboutMenuY)
    });

    var li_obj = document.querySelector('.about-menu-mob li.curent_link');
    document.querySelector('.about-menu-mob li.curent_link').remove();
    document.querySelector('.about-menu-mob ul').prepend(li_obj);

    document.querySelectorAll('.about-menu-mob .curent_link a').forEach(link => 
        link.addEventListener('click', (e) => {
            e.preventDefault();
            document.querySelector('.about-menu-mob').classList.toggle('about-menu-mob--visible');
        })
    )

    window.addEventListener('scroll', (e) => {
        var attr_mav = document.querySelector('nav.about-menu-mob');
        if(attr_mav.classList.contains('about-menu-mob--visible')) {
            attr_mav.classList.remove('about-menu-mob--visible');
        }
    });
}

const aboutGallery = document.querySelector('.section-about-gallery__inner')

if ( aboutGallery ) {
    new Swiper('.section-about-gallery__inner', {
        grabCursor: true,
        allowTouchMove: true,
        slidesPerView: 'auto',
        spaceBetween: 0
    })
}

/* Нижнее меню в about */
const aboutFooterImageContainer = document.querySelector('.section-about-footer__image-container');
const aboutFooterImage = document.querySelector('.section-about-footer__image-container img');
const aboutFooterLinks = document.querySelectorAll('.about-footer-menu a');
aboutFooterLinks.forEach((link, i) => {
    const src = link.dataset.image;
    if(src) {
        /* Preload images */
        const image = new Image();
        image.src = src;
        /* */

        link.addEventListener('mouseover', (e) => {
            aboutFooterImage.src = src;
            aboutFooterImageContainer.classList.add(`section-about-footer__image-container--${i + 1}`);
        })
        link.addEventListener('mouseout', (e) => {
            const activeLink = document.querySelector('.about-footer-menu__link--active a');
            if(activeLink) {
                aboutFooterImage.src = activeLink.dataset.image;
            }
            aboutFooterImageContainer.classList.value = 'section-about-footer__image-container';
        })
    }
});

