const swiperProjects = document.querySelector('.single-project__swiper')

if ( swiperProjects ) {
    new Swiper('.single-project__swiper', {
        slidesPerView: 1,
        grabCursor: true,
        pagination: {
            el: '.swiper-pagination',
        },
        navigation: {
            nextEl: '.custom-button-prev',
            prevEl: '.custom-button-next',
        },
    })
}

const swiperGallery = document.querySelector('.promo-gallery__swiper')

if (swiperGallery) {
    new Swiper('.promo-gallery__swiper', {
        slidesPerView: 1.6,
        grabCursor: true,
        spaceBetween: -80,
        effect: 'coverflow',
    
        // loop: true,
        // loopAdditionalSlides: 1,
        coverflowEffect: {
            rotate: 0,
            stretch: 0,
            scale: 0.65,
            depth: 0,
            modifier: 1,
            slideShadows: false,
        },
        navigation: {
            nextEl: '.promo-gallery__button-prev',
            prevEl: '.promo-gallery__button-next',
        },
        breakpoints: {
            320: {
                slidesPerView: 1,
                spaceBetween: 0,
                grabCursor: true,
                centeredSlides: true,
            },
            768: {
                slidesPerView: 1.4,
                spaceBetween: -60,
                grabCursor: true,
                centeredSlides: true,
            },
            992: {
                slidesPerView: 1.6,
                grabCursor: true,
                spaceBetween: -80,
                centeredSlides: true,
            },
        },
    })
}

