const swiperContact = document.querySelector('.section-contact-slider')

if ( swiperContact ) {
    new Swiper('.section-contact-slider__swiper', {
        slidesPerView: 1.6,
        grabCursor: true,
        // spaceBetween: -80,
    
        pagination: {
            el: '.swiper-pagination',
            type: 'fraction',
        },
    
        navigation: {
            nextEl: '.section-contact-slider__next',
            prevEl: '.section-contact-slider__prev',
        },
    
        pagination: {
            el: '.section-contact-slider__pagination',
            type: 'fraction',
            renderFraction: (currentClass, totalClass) => {
                console.log(currentClass, totalClass)
                return `<span class="${currentClass}"></span> / <span class="${totalClass}"></span>`
            },
        },
        
        breakpoints: {
            320: {
                slidesPerView: 1.3,
                grabCursor: true,
            },
            768: {
                slidesPerView: 1.5,
                grabCursor: true,
            },
            992: {
                slidesPerView: 1.6,
                grabCursor: true,
            },
        },
    })
}

