const block = document.querySelector('.section-faq-page-accordion')

if (block) {
    const blockListBtns = document.querySelectorAll('.js-section-faq-page-accordion')
    const blockListWraps = document.querySelectorAll('.js-section-faq-page-wrap')
    const blockPlusIcon = document.querySelectorAll('.js-section-faq-page-accordion')

    for (let i = 0; i < blockListWraps.length; i++) {
        const rateListWrap = blockListWraps[i]
        const itemListBtn = blockListBtns[i]
        const blockPlusIcon = blockListBtns[i]

        itemListBtn.addEventListener('click', function () {
            rateListWrap.classList.toggle('active')
            blockPlusIcon.classList.toggle('active')
        })
    }
}