const block = document.querySelector('.design-portfolio-gallery')

if (block) {
    const rotateBlock = document.querySelector('.js-scroll-rotate')

    // bg color intro
    const bgColorIntroBlock = document.querySelector(
        '.js-scroll-modify-color-alpha-intro'
    )

    // bg color outro
    // const bgColorOutroBlock = document.querySelector(
    //     '.js-scroll-modify-color-alpha-outro'
    // )

    // items up
    const translateUpBlockItems1 = document.querySelector(
        '.js-scroll-modify-translate-up-1'
    )
    const translateUpBlockItems2 = document.querySelector(
        '.js-scroll-modify-translate-up-2'
    )
    const translateUpBlockItems3 = document.querySelector(
        '.js-scroll-modify-translate-up-3'
    )

    // items down
    const translateDownBlockItems1 = document.querySelector(
        '.js-scroll-modify-translate-down-1'
    )
    const translateDownBlockItems2 = document.querySelector(
        '.js-scroll-modify-translate-down-2'
    )
    const translateDownBlockItems3 = document.querySelector(
        '.js-scroll-modify-translate-down-3'
    )

    let lastScroll = 0
    let isScrollingDown = true
    let requestId

    function changePropertyOnScroll() {
        const currentScroll = window.pageYOffset
        const maxScroll =
            document.documentElement.scrollHeight - window.innerHeight
        const scrollRatio = currentScroll / maxScroll

        const rotateDeg = isScrollingDown
            ? Math.min(-14, scrollRatio * -14)
            : Math.max(0, 0 - scrollRatio * 0)

        const scaleValue = isScrollingDown
            ? Math.min(0.65, 0.65 + scrollRatio)
            : Math.max(1, 1 - scrollRatio)

        const alphaValueIntro = isScrollingDown
            ? Math.min(0.1, 0.1 + scrollRatio)
            : Math.max(0.5, 0.5 - scrollRatio)

        // const alphaValueOutro = isScrollingDown
        //     ? Math.min(0.5, 0.5 + scrollRatio)
        //     : Math.max(0.07, 0.07 - scrollRatio)

        const translateValueUp1 = isScrollingDown
            ? `${Math.min(-10, scrollRatio * -10)}%`
            : `-${Math.min(0, scrollRatio * 0)}%`

        const translateValueUp2 = isScrollingDown
            ? `${Math.min(-14, scrollRatio * -14)}%`
            : `-${Math.min(0, scrollRatio * 0)}%`

        const translateValueDown1 = isScrollingDown
            ? `${Math.min(10, scrollRatio * 10)}%`
            : `-${Math.min(0, scrollRatio * 0)}%`

        const translateValueDown2 = isScrollingDown
            ? `${Math.min(18, scrollRatio * 18)}%`
            : `-${Math.min(0, scrollRatio * 0)}%`

        // bg color intro
        bgColorIntroBlock.style.backgroundColor = `rgba(0, 0, 0, ${alphaValueIntro})`

        // bg color outro
        // bgColorOutroBlock.style.backgroundColor = `rgba(0, 0, 0, ${alphaValueOutro})`

        rotateBlock.style.transform = `scale(${scaleValue}) rotate(${rotateDeg}deg)`

        // items up
        translateUpBlockItems1.style.transform = `translateY(${translateValueUp1})`
        translateUpBlockItems2.style.transform = `translateY(${translateValueUp2})`
        // translateUpBlockItems3.style.transform = `translateY(${translateValueUp})`

        // items down
        translateDownBlockItems1.style.transform = `translateY(${translateValueDown1})`
        translateDownBlockItems2.style.transform = `translateY(${translateValueDown2})`
        // translateDownBlockItems3.style.transform = `translateY(${translateValueDown})`

        if (
            currentScroll === maxScroll ||
            currentScroll === 0 ||
            (isScrollingDown && scaleValue === 2 && alphaValue === 1) ||
            (!isScrollingDown && scaleValue === 1 && alphaValue === 0)
        ) {
            window.cancelAnimationFrame(requestId)
        } else {
            requestId = window.requestAnimationFrame(changePropertyOnScroll)
        }
    }

    window.addEventListener('scroll', () => {
        if (window.scrollY > 10) {
            const currentScroll = window.pageYOffset
            if (currentScroll > lastScroll) {
                isScrollingDown = true
            } else {
                isScrollingDown = false
            }
            lastScroll = currentScroll
            requestId = window.requestAnimationFrame(changePropertyOnScroll)
        }
    })
}