const block = document.querySelector('.section-project-composition')

if (block) {
    const blockProjectCompositionImg = document.getElementsByClassName(
        'js-project-composition-img-bg'
    )

    new simpleParallax(blockProjectCompositionImg, {
        delay: 0.8,
        orientation: 'down',
        scale: 1.1,

    })
    
    const blockProjectCompositionImgMobile = document.getElementsByClassName(
        'js-project-composition-img-bg-mobile'
    )

    new simpleParallax(blockProjectCompositionImgMobile, {
        delay: 0.8,
        orientation: 'down',
        scale: 1.1,
    })
}
