const block = document.querySelector('.section-advantages')

if (block) {

    window.addEventListener("scroll", onScroll);

    function onScroll() {
        elementVisible(block);
    }
    
    function elementVisible(el) {
      let top = el.offsetTop; // верхняя точка блока
      let height = el.offsetHeight; // высота блока
      let bottom = top + height; // нижняя точка блока
      console.log(document.querySelector('.section-advantages').offsetTop)
    

      let blockTop = block.getBoundingClientRect().top
      let blockHeight = block.offsetHeight
      let windowHeight = window.innerHeight
      console.log(blockTop)
      
      let scrollPercentage = ((windowHeight - blockTop) / (windowHeight + blockHeight)) * 2
      
      //   let IsOverBottom = (top) > (window.pageYOffset + window.innerHeight);
      //   let IsOverBottom = (top + (height / 3)) > (window.pageYOffset + window.innerHeight);
      
      //   let IsBeforeTop = (bottom) < window.pageYOffset;
        let IsBeforeTop = (bottom - (height)) < window.pageYOffset;
      
    //   if (!(blockTop > 0) && ((blockTop - height) < 0) ) {
        if (!(blockTop - blockHeight > 0) && !IsBeforeTop ) {

        let block = el.querySelector('.js-paralax-left')

        let blockWidth = 0
        if (window.innerWidth > 520) {
            let wrapWidth = window.getComputedStyle(document.querySelector('.wrap')).width
            let wrapPadding = window.getComputedStyle(document.querySelector('.wrap')).padding.split(' ', )

            blockWidth = wrapWidth.replace('px', '') - (wrapPadding[1].replace('px', '') * 2)

            breakpoint = blockWidth - block.offsetWidth
            let leftPosition = Math.min(
                (breakpoint - (breakpoint * scrollPercentage)) ,
                breakpoint
            )
    
            block.style.left = leftPosition + 'px'
        } 

      } else {
        el.querySelector('.js-paralax-left').style.left = '0px'
        }

    }
}
