const block = document.querySelector('.header-new')

if (block) {
    const blockBody = document.querySelector('body')
    const blockHeaderNew = document.querySelector('.header-new')

    const hamburgerBtn = document.querySelector('.header-new-hamburger__btn')
    const hamburgerBtnWrap = document.querySelector(
        '.header-new-hamburger__btn-wrap'
    )

    const classActive = 'active'
    const classMobileActive = 'mobile-active'

    hamburgerBtn.addEventListener('click', function () {
        hamburgerBtnWrap.classList.toggle(classActive)
        blockBody.classList.toggle(classActive)
        blockHeaderNew.classList.toggle(classMobileActive)
    })

    document
        .querySelector('.header-new__overflow')
        .addEventListener('click', function (event) {
            if (!event.target.closest('.header-new__blocks')) {
                hamburgerBtnWrap.classList.remove(classActive)
                blockBody.classList.remove(classActive)
                blockHeaderNew.classList.remove(classMobileActive)
            }
        })
}
