const promoHead = document.querySelector('.promo-blocks .promo-gallery__head')
const promoSlider = document.querySelector('.promo-blocks .promo-gallery__swiper .swiper-wrapper')

if (promoHead && promoSlider) {
  promoHead.style.paddingLeft = `${promoSlider.getBoundingClientRect().x - promoHead.getBoundingClientRect().x}px`
}