const block = document.querySelector('.section-rate')

if (block) {
    const rateListWraps = document.querySelectorAll('.js-rate-list__wrap')
    const itemListBtns = document.querySelectorAll('.js-item-list-btn')

    for (let i = 0; i < rateListWraps.length; i++) {
        const rateListWrap = rateListWraps[i]
        const itemListBtn = itemListBtns[i]

        itemListBtn.addEventListener('click', function () {
            rateListWrap.classList.toggle('active')
        })
    }
}