const block = document.querySelector('.header-new')

if (block) {
    const rateListWraps = document.querySelectorAll(
        '.js-header-new-menu-sub-btn'
    )
    const itemListBtns = document.querySelectorAll(
        '.js-header-new-menu-sub-btn'
    )

    for (let i = 0; i < itemListBtns.length; i++) {
        const itemListBtn = itemListBtns[i]

        itemListBtn.addEventListener('click', function () {
            const rateListWrap = rateListWraps[i]
            const isActive = rateListWrap.classList.contains('active')

            // Удалить класс 'active' у всех rateListWraps
            rateListWraps.forEach((rateListWrap) => {
                rateListWrap.classList.remove('active')
            })

            // Добавить класс 'active' только выбранному rateListWrap, если он не активен
            if (!isActive) {
                rateListWrap.classList.add('active')
            }
        })
    }

    document.addEventListener('click', function (event) {
        const target = event.target

        // Проверить, был ли клик сделан вне кнопки itemListBtn и ее родительских элементов
        const isOutsideClick = !target.closest('.js-header-new-menu-sub-btn')

        if (isOutsideClick) {
            // Удалить класс 'active' у всех rateListWraps
            rateListWraps.forEach((rateListWrap) => {
                rateListWrap.classList.remove('active')
            })
        }
    })
    
    document
    .querySelector('.header-new__overflow')
    .addEventListener('click', function (event) {
        if (!event.target.closest('.header-new__blocks')) {
            const target = event.target

            const isOutsideClick = !target.closest('.js-header-new-menu-sub-btn')
    
            if (isOutsideClick) {
                rateListWraps.forEach((rateListWrap) => {
                    rateListWrap.classList.remove('active')
                })
            }
        }
    })
}
