const itemSections = document.querySelectorAll('.section-portfolio-item__wrap')


if (itemSections.length > 0) {
    itemSections.forEach(item => {
        const tabBtns = item.querySelectorAll('.js-tab-button')
        const tabContent = item.querySelectorAll('.js-tab-content')

        tabContent.forEach(content => {
            content.classList.add('noactive')
            content.classList.remove('block')
        })

        tabBtns.forEach((btn, index) => {
            btn.addEventListener('click', (event) => {
                tabBtns.forEach(btn => {
                    btn.classList.remove('active')
                })
                tabContent.forEach(content => {
                    content.classList.add('noactive')
                    content.classList.remove('block')
                })

                event.target.classList.add('active')

                item.querySelector(`[data-content="${event.target.dataset.tab}"]`).classList.remove('noactive')
                item.querySelector(`[data-content="${event.target.dataset.tab}"]`).classList.add('block')
            })

            index == 0 ? btn.click() : ''
            
        })


    })
}
