const block = document.querySelector('.header-new')

if (block) {
    const btns = document.querySelectorAll('.header-new-menu__list-item--arrow')

    btns.forEach((btn) => {
        btn.addEventListener('mouseover', () => {
            btn.classList.add('active-arrow-up')
        })

        btn.addEventListener('mouseout', () => {
            btn.classList.remove('active-arrow-up')
        })
    })
}
