const accordionItems = document.querySelectorAll('.accordion__item')

accordionItems && accordionHandler()

function accordionHandler() {
  accordionItems.forEach((element) => {
    element.querySelector('.accordion__header').addEventListener('click', () => {
      if (!element.classList.contains('active')) {
        accordionItems.forEach(el => {
          el.classList.remove('active')
        })
        element.classList.add('active')
        setTimeout(() => {
          element.classList.add('active')
        }, 300)
      }

      const accordBlock = element.closest('.faq__accordion-block')
      accordBlock.querySelector('.faq__more').innerHTML = element.querySelector('.accordion__more').innerHTML
    })
  })
}