const textBlock = document.querySelector('.good-top-e')
const textBlock2 = document.querySelector('.good-top__bottom')

if ( textBlock && textBlock2) {
  const textSample = textBlock.innerHTML.replace(/(<([^>]+)>)/gi, ' ').split(' ').map((item,index) => `<span class="fade" data-wow-offset="${100 + (50* (index + 1))}" data-wow-delay="${0.25 * (index)}s">${item}</span>`)

  // console.log(textBlock.innerHTML.split(' '))
  
  const textSample2 = textBlock2.innerHTML.split(' ').map((item,index) => `<span class="fade2" data-wow-offset="${50 + (20 * (index + 1))}" data-wow-delay="${0.05 * (index)}s">${item}</span>`)
  
  // const onePartHeight = document.documentElement.clientHeight / textSample.length
  
  textBlock.innerHTML = textSample.join(' ')
  textBlock2.innerHTML = textSample2.join(' ')
  
  var wow = new WOW(
    {
      boxClass:     'fade',      // animated element css class (default is wow)
      animateClass: 'fade--animated', // animation css class (default is animated)
      offset:       200,          // distance to the element when triggering the animation (default is 0)
      mobile:       true,       // trigger animations on mobile devices (default is true)
      live:         true,       // act on asynchronously loaded content (default is true)
      callback:     function(box) {
        // the callback is fired every time an animation is started
        // the argument that is passed in is the DOM node being animated
      },
      scrollContainer: null,    // optional scroll container selector, otherwise use window,
      resetAnimation: false,     // reset animation on end (default is true)
    }
  );
  wow.init();
  
  var wow2 = new WOW(
    {
      boxClass:     'fade2',      // animated element css class (default is wow)
      animateClass: 'fade--animated', // animation css class (default is animated)
      offset:       200,          // distance to the element when triggering the animation (default is 0)
      mobile:       true,       // trigger animations on mobile devices (default is true)
      live:         true,       // act on asynchronously loaded content (default is true)
      callback:     function(box) {
        // the callback is fired every time an animation is started
        // the argument that is passed in is the DOM node being animated
      },
      scrollContainer: null,    // optional scroll container selector, otherwise use window,
      resetAnimation: false,     // reset animation on end (default is true)
    }
  );
  wow2.init();
}