const servicesItems = document.querySelectorAll('.presentation__list-item')

if (servicesItems.length) {
  /* lazy load for smooth mobile animations */
  const serviceImages = document.querySelectorAll('.presentation-info__picture img')
  serviceImages.forEach(item => {
    let img = new Image;
    img.src = item.src;
  })
  servicesItems.forEach(item => {
    item.addEventListener('mouseenter', (event)=> {
      document.querySelector(`.presentation__list-item.active`).classList.remove('active')
      document.querySelector(`.presentation__link.active`).classList.remove('active')
      document.querySelector(`.presentation-info.active`).classList.remove('active')
      event.target.querySelector('.presentation__link').classList.add('active')
      item.classList.add('active')

      const name = event.target.dataset.service
      document.querySelector(`.presentation-info[data-service=${name}]`).classList.add('active')
    })
    item.addEventListener('mouseleave', (event) => {
      item.dataset.active = 0;
    });
  })

  document.querySelectorAll('.presentation__list-item a').forEach((link) => {
    link.addEventListener('click', (e) => {
      if(window.innerWidth <= 520) {
        let item = e.target.closest('.presentation__list-item');
        if(item.dataset.active != 1) {
          e.preventDefault();
          item.dataset.active = 1;
        }
      }
    })
  })
  
  document.querySelector(`.presentation-info`).classList.add('active')
  document.querySelector(`.presentation__link`).classList.add('active')
  document.querySelector(`.presentation__list-item`).classList.add('active')
}