const block = document.querySelector('.section-discount-head')

if (block) {
    const blockDiscountHeadTextUp = document.getElementsByClassName(
        'js-discount-head-text-up'
    )

    new simpleParallax(blockDiscountHeadTextUp, {
        delay: 0.6,
        orientation: 'up',
        scale: 1.6,
    })
    
    const blockPromoHeadTextUp = document.getElementsByClassName(
        'js-promo-head-text-up'
    )

    new simpleParallax(blockPromoHeadTextUp, {
        delay: 0.6,
        orientation: 'up',
        scale: 1.6,
        overflow: true
    })
    
    const blockDiscountHeadImg = document.getElementsByClassName(
        'js-scale-discount-head'
    )

    new simpleParallax(blockDiscountHeadImg, {
        delay: 0.8,
        orientation: 'down',
        scale: 1.6,
    })
}