const block = document.querySelector('.section-facade-design-slider')

if (block) {
    function initializeSlider(slider, img, dragLine) {
        slider.oninput = () => {
          let sliderVal = slider.value
          dragLine.style.left = sliderVal + '%'
          img.style.width = sliderVal + '%'
        }
      }
    
      const sliders = document.querySelectorAll('.js-facade-design-slider-switch input')
      const images = document.querySelectorAll('.js-facade-design-slider-images .js-facade-design-slider-img-after')
      const dragLines = document.querySelectorAll('.js-facade-design-slider-switch .js-facade-design-slider-drag-line')
      
      for (let i = 0; i < sliders.length; i++) {
        initializeSlider(sliders[i], images[i], dragLines[i])
      }
}
