const block = document.querySelector('.design-portfolio__title')

if (block) {
    window.addEventListener('scroll', function () {
        let blocks = document.querySelectorAll(
            '.design-portfolio__head-wrap'
        )

        blocks.forEach(function (block) {
            let blockModTextColor = block.querySelector(
                '.js-scroll-modify-text-color'
            )

            let blockTop = block.getBoundingClientRect().top
            let blockHeight = block.offsetHeight
            let windowHeight = window.innerHeight

            let scrollPercentage =
                ((windowHeight - blockTop) / (windowHeight + blockHeight)) * 100

            let settingsMeasurement = [
                {
                    element: blockModTextColor,
                    property: 'width',
                    min: 0,
                    max: 100,
                    unit: '%',
                },
            ]

            settingsMeasurement.forEach(function (setting) {
                let scrollValue = Math.min(
                    setting.max,
                    Math.max(setting.min, scrollPercentage)
                )
                setting.element.style[setting.property] =
                    scrollValue + setting.unit
            })
        })
    })
}