const block = document.querySelector('.section-portfolio__items')

if (block) {
    window.addEventListener('scroll', function () {
        const blocks = document.querySelectorAll('.section-portfolio__item')

        blocks.forEach(function (block) {
            let blockItem = block.querySelector(
                '.section-portfolio__item-trigger'
            )
            let blockPosition = block.getBoundingClientRect().top
            let windowHeight = window.innerHeight

            if (blockPosition < windowHeight) {
                blockItem.classList.add('active')
            }
        })
    })
}
